
import axios from 'axios';
import Loading from 'vux/src/components/loading/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Loading,
XButton,
Flexbox,
FlexboxItem
},
name: 'order',
data () {
return {
movieImg:[],
show:false
}
},
methods:{
loading(){
this.show = true;
axios.get('https://api.douban.com/v2/movie/in_theaters')
.then( (response)=> {
this.movieImg = response.data.subjects
this.show = false;
})
}
},
mounted(){


}
}



